<template>
    <div>
        <ul class="section">
            <li class="matter" v-if="type > 0">
                <div class="radio-box address choose">
                    <span :class="['img',0==id ? 'radio-down' : 'none']"></span>
                    <input type="radio" value="0" v-model="id" @click="setAddress('不限')"/>
                    <div class="inline-self">
                        <span class="self ellipsis2">不限</span>
                    </div>
                </div>
            </li>
            <li class="matter" v-for="item in items">
                <div class="radio-box address choose" v-if="type > 0">
                    <span :class="['img',item.id == id ? 'radio-down' : 'none']"></span>
                    <input type="radio" value="{{item.id}}" v-model="id" @click="setAddress(item.address)"/>
                    <div class="inline-self">
                        <span class="self ellipsis2">{{item.address}}</span>
                    </div>
                </div>
                <p class="address" v-else>
                    <span class="self ellipsis2">{{item.address}}</span></p>

                <span class="img del" @click="del(item.id)"></span>
                <span class="img write"
                      v-link="{name:'address-edit',query:{address:item.address,from:from},params:{id:item.id}}"></span>
            </li>
        </ul>
    </div>
</template>

<script>
    import {url, publish,order} from '../const'
    import $ from 'jquery'
    import {toast} from '../assets/js/toast'

    export default{
        props: {
            type: {type: Number, default: 0}
        },
        ready(){
            this.isChoose();
            this.loadAddress();
            this.$on('reload-child', function () {
                this.id = 0;
                this.address = '不限';
                this.from = '';
                this.isChoose();
                this.loadAddress();
            });
        },
        data(){
            return {
                items: [],
                id: 0,
                address: '不限',
                from: ''
            }
        },
        methods: {
            loadAddress(){
                this.$http.get(url.getAddressList).then(
                        function ({data:{data, success, resultMsg, code}}) {
                            if (data) {
                                this.items = data;
                                this.isFull();
                                /* console.log(JSON.stringify(data));*/
                                //选择默认地址
                                if (this.type > 0) {
                                    if (this.address != '') {
                                        let hasmatch = false;
                                        for (let i = 0; i < this.items.length; i++) {
                                            if (this.address == this.items[i].address) {
                                                this.id = this.items[i].id;
                                                this.address = this.items[i].address;
                                                hasmatch = true;
                                                break;
                                            }
                                        }
                                        if (!hasmatch && this.address != '不限') {
                                            //没有匹配的时候不会选中不限选项
                                            this.id = -1;
                                        }
                                    }
                                }
                            }
                            this.setAddress();
                        }, function (response) {
                            this.items = [];
                            toast.alert('系统异常');
                        }
                );
            },
            isChoose(){
                if (this.type > 0) {//choose
                    let query = this.$route.query;
                    console.log(JSON.stringify(query));
                    if (query.type && query.type > 0) {
                        this.type = query.type;
                        let p = query.targetType == 1?publish.get(this.type) : order.get(this.type);//区分发布还是协议
                        if (!p) {
                            if (query.targetType==1){
                                window.location.replace('#/publish');
                            }else{
                                window.location.replace('#/order');
                            }
                            return;
                        }
                        console.log(JSON.stringify(p));
                        this.address = p.address;
                    }
                }
                this.from = this.$route.path;
            },
            isFull(){
                this.$dispatch('full-status', this.items.length >= 5);
            },
            setAddress(address){
                if (address) {
                    this.address = address;
                }
                this.$dispatch('set-address', this.address);
            },
            del(id){
                let vm = this;
                toast.conform('确定删除?', function () {
                    vm.$http.get(url.deleteAddress, {id: id}).then(
                            function ({data}) {
                                if (data.success) {
                                    toast.alert('删除成功');
                                    location.reload(true);
                                } else {
                                    toast.alert(data.resultMsg);
                                }
                            }
                    )
                });
            }
        }
    }
</script>
<style scoped>
    .section .matter {
        height: auto;
    }

    .matter .address {
        width: 75%;
        height: 100%;
        padding: .15rem 0;
    }

    .address .self {
        display: -webkit-inline-box;
        line-height: normal;
        max-width: 100%;
        vertical-align: middle;
    }

    .choose .inline-self {
        display: inline-block;
        vertical-align: middle;
        padding-left: .25rem;
    }

    .inline-self .self {
        position: static;
        display: -webkit-inline-box;
        vertical-align: middle;
        max-width: 100%;
        margin: auto;
        line-height: normal;
    }

    .matter .img {
        position: absolute;
        top: .3rem;
        width: .22rem;
        height: .22rem;
        background-size: .22rem;
    }

    .img.del {
        right: .5rem;
        background-image: url(../assets/img/icon/del.png);
    }

    .img.write {
        right: .12rem;
        background-image: url(../assets/img/icon/edit.png);
    }

    .choose .img {
        position: absolute;
        left: 0;
        width: .18rem;
        height: .18rem;
    }

    .img.radio-down {
        background-size: .18rem;
        background-image: url(../assets/img/icon/selected03.png);
    }

    .img.none {
        border: .01rem solid #e5e5e5;
        border-radius: 50%;
    }
</style>



