<template>
  <div>
    <v-header>
    </v-header>

    <v-content :type="'form'">
      <v-address-list>
      </v-address-list>
    </v-content>

    <v-footer-btn>
   <!--    <input type="button" value="新增服务地址" :class="['input','max',isFull ? 'nk' : 'ok']" v-link="{name:'address-add',query:{from:from}}"/> -->
      <input type="button" value="新增服务地址" :class="['input','max',isFull ? 'nk' : 'ok']" @click="add"/>
    </v-footer-btn>
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import VAddressList from '../../components/addressList'
  import VFooterBtn from '../../components/FooterBtn'

  export default{
    route:{    
      data({from,to}){
        if(this.isReady){
          return {
            state:!this.state
          }
        }    
      }
    },     
    ready(){
      this.$on('ready',function(){
        this.from = this.$route.path;
      });
      this.$emit('ready');
      this.isReady = true;      
    },
    data(){
      return {
        isReady:false,
        state:false, 

        isFull: false,
        from:''
      }
    },
    methods: {
      add(){
        if (!this.isFull) {
           window.location.href = '#/set/address/add?' + this.from;
        }
      }
    },
    events: {
      'full-status': function (isFull) {
        this.isFull = isFull;
      }
    },
    watch:{
      'state':function(){
        this.$emit('ready');
        this.$broadcast('reload-child');
      }
    },     
    components: {VHeader, VContent, VAddressList, VFooterBtn},
  }
</script>





